// src/app/admin/module/page.tsx
"use client";
import React, { useEffect, useState } from "react";
import Link from "next/link";
import Cookies from "js-cookie";
import { Plus, Trash2, Loader2, BookOpen } from "lucide-react";
import { Module } from "@/types";

export default function AdminModulePage() {
  const [modules, setModules] = useState<Module[]>([]);
  const [loading, setLoading] = useState(true);

  // 1. Ambil URL Backend dari Environment
  const baseUrl = process.env.NEXT_PUBLIC_BASE_URL || "http://localhost:3000";

  const fetchModules = async () => {
    // 2. Ambil Token
    const token = Cookies.get("token");

    try {
      // 3. Gunakan baseUrl dinamis & Prefix /api & Header Auth
      const res = await fetch(`${baseUrl}/api/module`, { 
        cache: "no-store",
        headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${token}` // <--- Kirim token
        }
      });
      
      if (!res.ok) throw new Error("Gagal ambil data modul");
      setModules(await res.json());

    } catch (error) { 
        console.error(error); 
    } finally { 
        setLoading(false); 
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm("Yakin ingin menghapus modul ini?")) return;
    const token = Cookies.get("token");
    
    try {
        const res = await fetch(`${baseUrl}/api/module/${id}`, {
            method: "DELETE",
            headers: { "Authorization": `Bearer ${token}` }
        });
        
        if (res.ok) {
            fetchModules();
        } else {
            alert("Gagal menghapus modul");
        }
    } catch (error) {
        alert("Terjadi kesalahan koneksi");
    }
  };

  useEffect(() => { fetchModules(); }, []);

  return (
    <div>
      <div className="flex items-center justify-between mb-8">
        <div>
            <h1 className="text-3xl font-bold font-heading">Modul Pembelajaran</h1>
            <p className="text-gray-400 text-sm">Kelola materi dan modul kuliah.</p>
        </div>
        <Link href="/admin/module/create" className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg flex items-center gap-2 font-bold text-sm transition-colors">
            <Plus size={18} /> BUAT MODUL
        </Link>
      </div>

      <div className="bg-zinc-900 border border-white/10 rounded-xl overflow-hidden shadow-xl">
        {loading ? (
            <div className="p-12 text-center flex flex-col items-center gap-3 text-gray-500">
                <Loader2 className="animate-spin w-8 h-8 text-blue-500" />
                <span className="text-sm">Memuat modul...</span>
            </div>
        ) : modules.length === 0 ? (
            <div className="p-12 text-center text-gray-500">
                Belum ada modul pembelajaran.
            </div>
        ) : (
          <table className="w-full text-left border-collapse">
            <thead className="bg-white/5 text-gray-400 text-xs uppercase tracking-wider">
              <tr>
                <th className="p-4 border-b border-white/10">Nama Modul</th>
                <th className="p-4 border-b border-white/10">Deskripsi</th>
                <th className="p-4 border-b border-white/10 text-right">Aksi</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-white/10">
              {modules.map((item) => (
                <tr key={item.id} className="hover:bg-white/5 transition-colors">
                  <td className="p-4 font-medium text-white flex items-center gap-3">
                    <div className="w-8 h-8 rounded bg-blue-500/10 text-blue-400 flex items-center justify-center border border-blue-500/20 shrink-0">
                        <BookOpen size={16} />
                    </div>
                    {item.title}
                  </td>
                  <td className="p-4 text-gray-400 text-sm max-w-md">
                    <p className="line-clamp-2">{item.description}</p>
                  </td>
                  <td className="p-4 text-right">
                    <button onClick={() => handleDelete(item.id)} className="p-2 bg-zinc-800 text-red-400 border border-white/10 rounded-lg hover:bg-red-900/50 hover:text-red-400 hover:border-red-500/30 transition-all">
                        <Trash2 size={16} />
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}